svg.checkbox .outer-ring {
  stroke-dasharray: 375;
  stroke-dashoffset: 375px;
  animation: resetRing .35s ease-in-out forwards;
}

.switch:checked + svg.checkbox .outer-ring {
  animation: animateRing .35s ease-in-out forwards;
}

.switch:checked + svg.checkbox .is_checked {
  opacity: 1;
  transform: translateX(0)rotate(0deg);
}

.switch:checked + svg.checkbox .is_unchecked {
  opacity: 0;
  transform: translateX(-200%)rotate(180deg);
}

svg.checkbox {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

svg.checkbox .is_checked {
  opacity: 0;
  fill: #ff0;
  transform-origin: 50%;
  transition: all .35s;
  transform: translateX(200%)rotate(45deg);
}

svg.checkbox .is_unchecked {
  opacity: 1;
  fill: #fff;
  transform-origin: 50%;
  transition: all .35s;
  transform: translateX(0)rotate(0deg);
}

@keyframes animateRing {
  to {
    stroke-dashoffset: 0;
    stroke: #b0aa28;
  }
}

@keyframes resetRing {
  to {
    stroke-dashoffset: 0;
    stroke: #fff;
  }
}

